(function() {
  const langBtn = document.querySelector('.js-choice-lang');
  const langModal = document.querySelector('.choice-lang.modals');

  const toggleLang = () => {
    langModal.classList.contains('modals--open') ? langModal.classList.remove('modals--open') : langModal.classList.add('modals--open')
  };

  langBtn.addEventListener('click', function(e) {
    e.preventDefault();
    toggleLang();
  })
})();
