// import Swiper from "swiper";

(() => {
	if (!$(".js-to-swiper").length) return false;

	const breakpoint = window.matchMedia("(min-width: 581px)");
	let mySwiper;

	const breakpointChecker = function () {
		// if larger viewport and multi-row layout needed
		if (breakpoint.matches === true) {
			// clean up old instances and inline styles when available
			if (mySwiper !== undefined) mySwiper.destroy(true, true);
			// or/and do nothing
			return;
			// else if a small viewport and single column layout needed
		} else if (breakpoint.matches === false) {
			// fire small viewport version of swiper
			return enableSwiper();
		}
	};

	const enableSwiper = function () {
		mySwiper = new Swiper(".js-to-swiper", {
			loop: true,
			slidesPerView: 1,
			keyboardControl: true,
			grabCursor: true,
			// pagination
			navigation: {
				nextEl: ".arrow-nav_right",
				prevEl: ".arrow-nav_left",
			},
			pagination: {
				clickable: true,
				// dynamicBullets: false,
				el: ".pagination",
			},
		});
	};

	// keep an eye on viewport size changes
	breakpoint.addListener(breakpointChecker);
	// kickstart
	breakpointChecker();
})($);

(() => {
	// Video play

	$(".js-play").click(function () {
		let video = $(this).data("video");
		$(this).append(video);
	});

	$(".btn-nav").click(function () {
		$(".fotogallery__video iframe, .fotogallery__video video").remove();
	});
})($);

var modal_catalog_list;

(() => {
	if (!$(".js-catalog-list").length) return false;

	$(".js-catalog-list").each((i, el) => {
		let swiper = $(el).find(".swiper-container")[0];
		let prev = $(el).find(".slider-arrow-nav__button_prev")[0];
		let next = $(el).find(".slider-arrow-nav__button_next")[0];
		let breakpoints = {
			0: {
				slidesPerView: 1,
			},
			581: {
				slidesPerView: 2,
			},
			769: {
				slidesPerView: 3,
			},
		};

		let spaceBetween = 10;

		if ($(el).hasClass("full-width")) {
			breakpoints = {
				0: {
					slidesPerView: 1,
				},
				581: {
					slidesPerView: 2,
				},
				769: {
					slidesPerView: 3,
				},
				1025: {
					slidesPerView: 4,
				},
			};

			spaceBetween = 20;
		}

		modal_catalog_list = new Swiper(swiper, {
			loop: true,
			slidesPerView: 3,
			keyboardControl: true,
			spaceBetween: spaceBetween,
			grabCursor: true,
			navigation: {
				nextEl: next,
				prevEl: prev,
			},
			breakpoints: breakpoints,
		});
	});
})($);

var modal_catalog_preview, modal_catalog_thumbnails;

(() => {
	if (!$(".js-modal-catalog-preview").length) return false;

	modal_catalog_thumbnails = new Swiper(".js-modal-catalog-thumbnails", {
		loop: false,
		slidesPerView: "auto",
		keyboardControl: true,
		spaceBetween: 10,
		grabCursor: true,
		watchSlidesVisibility: true,
		watchSlidesProgress: true,
	});
	modal_catalog_preview = new Swiper(".js-modal-catalog-preview", {
		loop: false,
		slidesPerView: 1,
		keyboardControl: true,
		spaceBetween: 10,
		grabCursor: true,
		navigation: {
			nextEl: ".btn-next",
			prevEl: ".btn-back",
		},
		thumbs: {
			swiper: modal_catalog_thumbnails,
		},
		pagination: {
			el: ".img-slider__counter",
			type: "fraction",
		},
		breakpoints: {
			0: {
				// spaceBetween: 0,
				slidesPerView: "auto",
			},
			851: {
				slidesPerView: 1,
				spaceBetween: 10,
			},
		},
	});
})($);

(() => {
	const goToSwiperAll = () => {
		if ($(window).outerWidth() <= 580) {
			const mobileSliders = {
				singleTeamSlider: new Swiper(".single__team-slider", {
					loop: true,
					slidesPerView: 1,
					keyboardControl: true,
					freeMode: true,
					grabCursor: true,
					navigation: {
						nextEl: ".team .btn-nav_right",
						prevEl: ".team .btn-nav_left",
					},
					pagination: {
						el: ".team .counter",
						type: "fraction",
					},
				}),

				singleGalerySlider: new Swiper(".single__gallery-slider", {
					loop: true,
					slidesPerView: 1,
					keyboardControl: true,
					freeMode: true,
					grabCursor: true,
					navigation: {
						nextEl: ".single__gallery .btn-nav_right",
						prevEl: ".single__gallery .btn-nav_left",
					},
					pagination: {
						el: ".single__gallery .counter",
						type: "fraction",
					},
				}),

				singleNewsSlider: new Swiper(".single__news .search-result", {
					loop: true,
					slidesPerView: 1,
					keyboardControl: true,
					freeMode: true,
					grabCursor: true,
					navigation: {
						nextEl: ".single__news .btn-nav_right",
						prevEl: ".single__news .btn-nav_left",
					},
					pagination: {
						el: ".single__news .counter",
						type: "fraction",
					},
				}),

				keyslider: new Swiper(".keys__slider", {
					loop: true,
					slidesPerView: 1,
					freeMode: true,
					keyboardControl: true,
					grabCursor: true,
					navigation: {
						nextEl: ".keys .arrow-nav_right",
						prevEl: ".keys .arrow-nav_left",
					},
					pagination: {
						clickable: true,
						el: ".keys .pagination",
					},
				}),

				sertislider: new Swiper(".sertificate__slider", {
					loop: true,
					slidesPerView: 3,
					freeMode: true,
					keyboardControl: true,
					grabCursor: true,
					// pagination
					navigation: {
						nextEl: ".sertificate .btn-nav_right",
						prevEl: ".sertificate .btn-nav_left",
					},
					pagination: {
						el: ".sertificate__counter",
						type: "fraction",
					},
				}),
			};

			window.toSwiperSliders = {};

			$.each(mobileSliders, (key, value) => {
				if (value.$el) {
					window.toSwiperSliders[key] = value;
				}
			});
		}
	};

	goToSwiperAll();

	$(window).resize(() => {
		if ($(window).width() <= 580) {
			$.each(window.toSwiperSliders, function (key, value) {
				if (window.toSwiperSliders[key]) {
					window.toSwiperSliders[key].destroy();
				}
			});
			goToSwiperAll();
		} else {
			$.each(window.toSwiperSliders, function (key) {
				if (window.toSwiperSliders[key]) {
					window.toSwiperSliders[key].destroy();
				}
			});
		}
	});
})($);

