$(()=>{
	$(document).on('change', '[name="delivery-method"]', function() {
		let visible = $(this).val();

		if (visible=='delivery') {
			$('#delivery').addClass('active');
			$('#pickup').removeClass('active');
		} else {
			$('#delivery').removeClass('active');
			$('#pickup').addClass('active');
		}
	})

	$(document).on('change', '[name="who-method"]', function() {
		let visible = $(this).val();

		if (visible=='legal') {
			$('#legal').addClass('active');
		} else {
			$('#legal').removeClass('active');
		}
	})
})