// import Inputmask from "inputmask";
import validate from "jquery-validation";

$(() => {
	// var selector = document.querySelectorAll("input[name='phone']");

	// var im = new Inputmask({
	// 	"mask": "+7 (999) 999-99-99",
	// 	clearMaskOnLostFocus: true,
	// 	clearIncomplete: true
	// });

	// im.mask(selector);
	$(".input__item, .textarea__item")
		.on("focus", (e) => {
			let $input = $(e.target);
			$input.parent().addClass("is-focus");
		})
		.on("blur change", (e) => {
			let $input = $(e.target);

			if ($input.val() == "") $input.parent().removeClass("is-focus");
		});

	$("form:not(.is-default-form):not(.wpcf7-form)").each(function (i) {
		const $form = $(this);

		$form.validate({
			errorPlacement: function (error, element) {
				//just nothing, empty
			},
			highlight: (element, errorClass, validClass) => {
				$(element)
					.parent()
					.addClass(errorClass)
					.removeClass(validClass);
			},
			unhighlight: (element, errorClass, validClass) => {
				$(element)
					.parent()
					.removeClass(errorClass)
					.addClass(validClass);
			},
			submitHandler: (form) => {
				let data = $(form).serialize();

				$.ajax({
					type: "POST",
					url: $form.attr("action"),
					data: data,
					success: function (data) {
						$(form)[0].reset();
					},
				});
			},
			rules: {
				// phone: {
				// 	required: true,
				// 	minlength: 10,
				// },
			},
		});
	});
});
