//import Inputmask from "inputmask";
$(()=>{

	//  
	$(document).on("click", ".js-open-sidebar", ()=> {
		$(".js-open-sidebar").addClass("is-hidden");
		$("#menu-catalog").addClass("is-visible");
	})

	//  
	$(document).on("click", ".js-close-sidebar", ()=> {
		$("#menu-catalog").removeClass("is-visible");
		$(".js-open-sidebar").removeClass("is-hidden");
	})



	//  
	$(document).on("click", ".js-open-sidebar-filter", () => {
	    $(".js-open-sidebar-filter").addClass("is-hidden");
	    $("#menu-filter").addClass("is-visible");
	})

	//  
	$(document).on("click", ".js-close-sidebar-filter", () => {
		$(".js-open-sidebar-filter").removeClass("is-hidden");
		$("#menu-filter").removeClass("is-visible");
	})

	//   
	$(document).on("click", ".js-open-sidebar-navigation", () => {
	    $(".js-open-sidebar-navigation").addClass("is-hidden");
	    $("#myaccount_navigation").addClass("is-visible");
	})

	//   
	$(document).on("click", ".js-close-sidebar-navigation", () => {
		$(".js-open-sidebar-navigation").removeClass("is-hidden");
		$("#myaccount_navigation").removeClass("is-visible");
	})


	$(document).on('input propertychange blur', 'textarea', function() {
		$(this).parent().find('.textarea__placeholder').show();

		if(this.value.length){
			$(this).parent().find('.textarea__placeholder').hide();
		}
	});
	$(document).on('focus', 'textarea', function() {
		$(this).parent().find('.textarea__placeholder').hide();
		if(this.value.length){
			$(this).parent().find('.textarea__placeholder').hide();
		}
	});

	$(document).on('click', '.js-return-false', function() {
		return false
	});

	$('.block_text_hide').each(function() {
	    let holder = $(this).find('.text_hide'),
	        button = $(this).find('.text_hide_active'),
	        text = holder.html(),
	        href = $(this).attr('href'),
	        symbolsForHide = 9,
	        show = () => {
	            holder.html(text);
	            button.removeClass('show');
	            $(this).attr('href', href);
	            $(this).removeClass('js-return-false');
	            setTimeout(() => button.remove(), 500);
	        },
	        hide = () => {
	            holder.html(text.replace(new RegExp('(.+).{' + symbolsForHide + '}$'), "$1" +'<span class="text_hide_blur">'+ 'x'.repeat(symbolsForHide) + '</span>'));
	            $(this).attr('href', '#');
	            button.addClass('show').text('');
	        }
	    button.click(function() {
	        if ($(this).hasClass('show')) {
				show();
				return false
	        } else {
	        	hide()
	        }
	    });
	    hide();
	})
})