$(() => {
	var $menu = $(".header__mobile");

	if (!$menu.length) return false;

	$(document).on("click", ".js-toggle-menu", (e) => {
		e.preventDefault();

		let $this = $(e.currentTarget);

		$menu.toggleClass("is-visible");
		$this.toggleClass("is-active");

		$("html, body").toggleClass("js-lock");

		$(".header__panel").removeClass("header__panel_visible");
		$(".header__panel-item").removeClass("header__panel-item_active");
		$(".js-submenu").removeClass("is-hover");
	});

	function menuHeight() {
		let winHeight = $(window).height(),
			headerHeight = $('.header__top').outerHeight(),
			navHeadHeight = $('.catalog-nav__head').outerHeight(),
			navBodyHeight = winHeight-headerHeight-navHeadHeight;

		$('.catalog-nav__body').css('height',navBodyHeight);

	}

	$(window).on("load resize", () => {
		let ww = $(window).width();
		if (ww > 1024) {
			$(".js-toggle-menu").removeClass("is-active");
			$menu.removeClass("is-visible");
		}
		menuHeight();
	});

	$('.js-catalog-show').on("click mouseenter", () => {
		menuHeight();
	});

	$('.js-close-catalog').click(function(){
		$('.header__panel-bg').click();
	});

	$(".catalog-nav__list-link").mouseenter(function(){

		let contentSubmenu = $(this).closest('.catalog-nav__list-item').find('.catalog-nav__submenu').clone();

		if ( $(this).hasClass('is-active') ) {

		} else {
			if ($(window).innerWidth() > 1024) {
				$(".catalog-nav__list-link.is-active").removeClass("is-active");
				$(this).addClass("is-active");
				$(".catalog-nav__submenu-content").html(contentSubmenu);
			}
		}

	});

	if ($(window).innerWidth() < 1025) {
		$(".catalog-nav__list-item").click(function () {
			if ($(this).hasClass("is-active")) {
				$(this).removeClass("is-active");
			} else {
				$(".catalog-nav__list-item.is-active").removeClass("is-active");
				$(this).addClass("is-active");
			}
		});
	}
});