(() => {
    if (!$(".select").length) return false;

    let $fullpage = $(".fullpage__part_fluid");

    $(document).on("click", ".select__label", (e) => {
        let $select = $(e.currentTarget).closest(".select");

        $select.toggleClass("is-active");
        $select.find('.select__list').fadeToggle(300);
        $fullpage.toggleClass("is-front");
    });

    $(document).on("click", ".select__list-item", (e) => {
        let select = $(e.currentTarget).closest(".select");
        let input = select.find(".select__input");
        let label = $.trim($(e.currentTarget).text());
        let val = $(e.currentTarget).attr("data-value")
            ? $(e.currentTarget).data("value")
            : label;

        select.find(".select__list-item").removeClass("is-selected");
        $(e.currentTarget).addClass("is-selected");

        if ($(e.currentTarget).hasClass("is-reverse")) {
            select.addClass("is-reverse");
        } else {
            select.removeClass("is-reverse");
        }

        if (!$(e.currentTarget).find(".icon").length) {
            select.addClass("is-hideicon");
        } else {
            select.removeClass("is-hideicon");
        }

        select.removeClass("is-active");
        select.find('.select__list').fadeToggle(300);
        $fullpage.removeClass("is-front");
        select.find(".select__label span").text(label);
        input.val(val);
    });

    $(window).on("load resize", () => {
        let ww = $(window).width();

        if (ww <= 580) {
            $(".select").each((i, el) => {
                let help_width = $(el).find(".select__text").width() + 30;

                $(el)
                    .find(".select__label")
                    .css("padding-left", `${help_width}px`);
            });
        } else {
            $(".select__label").removeAttr("style");
        }
    });

    $(document).mouseup(function (e) {
        let container = $(".select__wrapper, .select-cat__wrapper");
        if (!container.is(e.target) && container.has(e.target).length === 0) {
            const $select = container.closest(".select");
            const $selectCat = container.closest(".select-cat");
            $select.removeClass("is-active");
            $selectCat.removeClass("is-active");
            $select.find('.select__list[style="display: block;"]').fadeToggle(300);
            $selectCat.find('.select-cat__list[style="display: block;"]').fadeToggle(300);
            $fullpage.removeClass("is-front");
        }
    });

    $(document).on("click", ".js-select-item", function () {
        let $select = $(this).closest(".js-select");

        $select.toggleClass("is-active");
        $select.find('.select-cat__list, .select-cat__bg').fadeToggle(300);
    });

})($);
