// import Swiper from "swiper";
import {
	modal_catalog_preview,
	modal_catalog_list,
	modal_catalog_thumbnails,
} from "./_toswiper.js";

$(() => {
	var is_opera = !!window.opera || navigator.userAgent.indexOf(" OPR/") >= 0;
	var is_Edge = navigator.userAgent.indexOf("Edge") > -1;
	var is_chrome = !!window.chrome && !is_opera && !is_Edge;
	var is_explorer =
		typeof document !== "undefined" && !!document.documentMode && !is_Edge;
	var is_firefox = typeof window.InstallTrigger !== "undefined";
	var is_safari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
	var isLoad = false;

	$(document).on("click", ".js-close-modal", (e) => {
		e.preventDefault();
		$.magnificPopup.close();
	});

	var modal_open = (e) => {
		$.magnificPopup.close();

		var modal = $(e.currentTarget).attr("href")
			? $(e.currentTarget).attr("href")
			: $(e.currentTarget).data("modal");

		var container_class = "is-modal-open";

		if ($(e.currentTarget).attr("data-youtube")) {
			$(modal + " iframe").attr(
				"src",
				"https://www.youtube.com/embed/" +
					$(e.currentTarget).data("youtube") +
					"?autoplay=1&showinfo=0&rel=0&controls=0"
			);
		}

		if ($(e.currentTarget).attr("data-input")) {
			$(modal + ' input[name="form"]').val(
				$(e.currentTarget).data("input")
			);
		}

		$.magnificPopup.open(
			{
				tClose: "",
				removalDelay: 600,
				fixedContentPos: true,
				fixedBgPos: true,
				overflowY: "hidden",
				closeMarkup:
					'<div class="modals__close close js-close-modal"><svg class="icon icon-close close" viewBox="0 0 612 612"><use xlink:href="/app/icons/sprite.svg#close"></use></svg></div>',
				mainClass: "css-modal-animate",
				items: {
					src: modal,
					type: "inline",
				},
				callbacks: {
					open: () => {
						if ($(modal).find(".js-modal-catalog-preview").length) {
							modal_catalog_thumbnails.update();
							modal_catalog_preview.update();
						}

						if ($(modal).find(".js-catalog-list").length)
							modal_catalog_list.update();
					},
					beforeOpen: () => {
						if (is_safari) {
							setTimeout(function () {
								if (
									$("body").attr("data-body-scroll-fix") ==
										undefined ||
									$("body").attr("data-body-scroll-fix") == ""
								) {
									let scrollPosition =
										window.pageYOffset ||
										$(document).scrollTop();
									/*$('html').attr('style', `
                                                    overflow: hidden;
                                                    -webkit-overflow-scrolling: touch;`);*/
									$("body")
										.attr(
											"style",
											`
                                                overflow: hidden;
                                                position: fixed;
                                                left: 0;
                                                right: 0;
                                                width: 100%;
                                                top: -${scrollPosition}px`
										)
										.attr(
											"data-body-scroll-fix",
											scrollPosition
										);
								}
							}, 15);
						} else {
							$("body").addClass(container_class);
						}
					},
					beforeClose: () => {
						if (is_safari) {
							let scrollPosition = $("body").attr(
								"data-body-scroll-fix"
							);
							$("body")
								.attr("style", "")
								.removeAttr("data-body-scroll-fix");
							window.scroll(0, scrollPosition);
						} else {
							$("body").removeClass(container_class);
						}
					},
				},
			},
			0
		);
		if ($(e.target).hasClass("openModalRegisterTab")) {
			$("#modal-login-signin").hide();
			$("#modalFormLostPassword").hide();
			$("#modal-login-regis").show();
		}
	};

	$(document).on("click", ".js-modal", (e) => {
		e.preventDefault();

		if (!isLoad) {
			$.getScript("/app/js/modals.js", function (
				data,
				textStatus,
				jqxhr
			) {
				isLoad = true;
				modal_open(e);
			});
		} else {
			modal_open(e);
		}
	});

	$(document).on("click", ".js-gallery a", (e) => {
		e.preventDefault();
		if (!isLoad) {
			$.getScript("/app/js/modals.js", function (
				data,
				textStatus,
				jqxhr
			) {
				isLoad = true;
				let $this = $(e.currentTarget);
				let $gallery = $this.closest(".js-gallery");
				$gallery.magnificPopup("open");
			});
		}
	});

	$(document).on("click", ".js-img-modal a", (e) => {
		e.preventDefault();
		if (!isLoad) {
			$.getScript("/app/js/modals.js", function (
				data,
				textStatus,
				jqxhr
			) {
				isLoad = true;
				let $this = $(e.currentTarget);
				let $gallery = $this.closest(".js-gallery");
				$gallery.magnificPopup("open");
			});
		}
	});
});
